header{
    position:fixed;
    width:100%;
    height:106px;
    z-index: 999999;
    height:90px; 
}
nav{
    height:90px;
    box-shadow: 0px 3px 0px 0px #ff9438;
}
.nav-link{
    display: flex;
    justify-content: center;
}
.nav-link i{
    font-size: 19px;
    padding-left: 3px;
}
.navbar-brand{
    font-size: 30px;
}
.nav-item{
    position: relative;
    margin-left: 10px;
}
.nav-item__sublist{
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #fff;
    width: 60vw;
    flex-wrap: wrap;
    align-items: center;
    border-bottom: 1px solid #111;
}

.nav-item:hover .nav-item__sublist, .nav-item:focus .nav-item__sublist{
    display: flex;
}
.nav-item__sublist-item {
    display: block;
    width: 25%;
}
.nav-item .nav-item__sublist-item:hover a{
    color: rgb(209, 124, 25) !important;
    text-decoration: underline;
}
.nav-item__sublist-item a{
    display: block;
    text-align: center;
    padding: 10px 20px;
    text-transform: capitalize;
    font-weight: bold;
    color: #abb7c4;
}
.nav-link:hover, .nav-link:focus{
    font-weight: bold;
    color: #000 !important;
    background-color: #fff;
}
.nav-item:hover a, .nav-item:focus a{
    font-weight: bold;
    color: #000 !important;
    background-color: #fff;
}
.navbar-dark .navbar-nav .nav-link{
    color: #abb7c4;
}
.nav-item:hover, .nav-item:focus{
    font-weight: bold;
    color: #000;
    background-color: #fff;
}
.search-bar{
    display: inline-flex;
    position: relative;
}
.search-bar button{
    position: absolute;
    right: 5%;
    top: 20%;
    outline:none !important;
    border: none !important;
    background:none !important;
}
.search-bar button:hover{
    color: rgb(209, 124, 25);
}
.search-wrapper{
    width: 20%;
}
.search-wrapper form{
    width:100%
}
.search-bar{
    width:100%;
}